<template>
  <div>
    <van-sticky>
      <van-nav-bar title="立即购买" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <!-- 地址 -->
    <div class="address-container" @click="chooseAddress">
      <img class="location-img" src="@/assets/imgs/mall/location.png" />
      <div class="address-info" v-if="curAddress.name">
        <div class="address-info-top">
          <span>{{ curAddress.name }}</span>
          <span>{{ curAddress.phone }}</span>
        </div>
        <div class="address-info-bottom van-ellipsis">
          {{ curAddress.province }}{{ curAddress.city }}{{ curAddress.area
          }}{{ curAddress.detail }}
        </div>
      </div>
      <div v-else class="no-default-address">新增收货地址</div>
      <van-icon class="arrow-icon" name="arrow" />
    </div>
    <!-- 商品 -->
    <div class="goodsList">
      <div class="goodsItem">
        <img :src="slides" alt class="goodsImg leftEl" />
        <div class="leftEl goodsRight">
          <div class="goodsName">{{ goodsInfo.goods_name }}</div>
          <div class="goodsNum">
            <span>￥{{ goodsInfo.price }}</span>
            <van-stepper v-model="goodsNum" class="rightEl" @change="getValue" />
          </div>
          <div class="goodsWeight"></div>
          <!-- 商品规格:{{ chooseSize.remark }} -->
          <div class="goodsPrice rightEl">
            小计:
            <span>￥{{ (goodsInfo.price * goodsNum).toFixed(2) }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="payMid">
      <div class="midItem">
        运费
        <span class="midNum rightEl">{{ freight }}元</span>
      </div>
    </div>
    <div class="payMid">
      <van-cell-group>
        <van-field
          v-model="remarks"
          rows="2"
          autosize
          label="买家留言:"
          type="textarea"
          maxlength="50"
          placeholder="请输入留言"
          show-word-limit
        />
      </van-cell-group>
    </div>
    <!-- 底部 -->
    <div class="payBtm">
      <span>
        实际付款金额:
        <span class="totalPrice">￥{{ totalPrice }}</span>
      </span>
      <button @click="nowPay">立即购买</button>
    </div>
    <van-dialog
      v-model="showPassword"
      title="交易密码"
      show-cancel-button
      @confirm="pay"
      @cancel="cancelPay"
      :before-close="beforePasswordClose"
    >
      <van-password-input :value="password" info="密码为 6 位数字类型" style="position:initial" />
    </van-dialog>

    <van-number-keyboard
      id="keyboard-panel"
      :show="showPassword"
      @input="onInput"
      @delete="onDelete"
      :z-index="9999"
      style="color:black"
    />
  </div>
</template>

<script>
