    this.goodsInfo = this.goodsId;
    this.slides = this.goodsInfo.img;
    this.getTotal();
  },
  methods: {
    beforePasswordClose(action, done) {
      if (action === "confirm") {
        if (this.password.length != 6) {
          done(false);
        } else {
          done();
        }
      } else {
        done();
      }
    },
    onInput(key) {
      this.password = (this.password + key).slice(0, 6);
      this.zIndex = 9999;
    },
    onDelete() {
      this.password = this.password.slice(0, this.password.length - 1);
    },
    cancelPay() {
      this.password = "";
    },
    pay() {
      order({
        transaction: this.password,
        id: this.goodsInfo.id,
        num: this.goodsNum,
        remarks: this.remarks,
        totalMoney: this.totalPrice,
        address_id: this.curAddress.id,
        uid: this.id,
        freight: this.freight
      }).then(res => {
        this.password = "";
          console.log(res)
        if (res.errcode == 0) {
          this.$toast("下单成功");
          setTimeout(res => {
            this.$router.push({ name: "Mall" });
          }, 1000);
        } else {
          this.$toast(res.msg);
