      updateCart(id, value, size_id, size_name).then(res => {});
      this.getCartTotal();
    },
    // 购物车结算
    getCartTotal() {
      let totalPrice = 0;
      totalPrice += Number(this.goodsInfo.price) * Number(this.goodsInfo.nums);
    },
    getReduce() {
