    },
    // 立即支付
    getTotal() {
      // console.log(totalPrice)
      console.log(this.freight);
      this.totalPrice =
        Number(this.goodsInfo.price) * this.goodsNum +
        Number(this.freight);
      // 立即购买
      //   showFreight().then(res => {
      //     this.noFee = res.nofree;
      //     if (Number(totalPrice) >= Number(res.nofree)) {
