      //     }
      // this.totalPrice = (
      //   totalPrice -
      //       Number(this.ticketChoose.price || 0) -
      //       ((this.integralInput || 0) / this.config || 0) -
      //       (this.commissionInput || 0) +
      //       Number(this.freight)
      //     ).toFixed(2);
      //     this.getReduce();
      //     if (this.totalPrice < 0) {
      //       this.totalPrice = 0;
      //     }
      //   });
    },
    // 获取积分
    showIntegral() {
      this.integralShow = true;
      this.getIntegral();
    },
    getIntegral() {
      myIntegral().then(res => {
        this.myIntegral = res.integral;
      });
    },
    sendIntegral(action, done) {
      if (action == "confirm") {
        if (Number(this.integralInput) > Number(this.myIntegral)) {
          this.$toast("填写的抵扣积分不能大于拥有的积分！");
          done(false);
        } else {
          if (this.type == 1) {
            this.getTotal();
          } else {
            this.getCartTotal();
          }
          done();
        }
      } else {
        done();
      }
    },
    // 获取佣金
    showCommission() {
      this.commissionShow = true;
      this.getCommission();
    },
    getCommission() {
      myCommission().then(res => {
        this.myCommission = res.commission;
      });
    },
    sendCommission(action, done) {
      if (action == "confirm") {
        if (parseInt(this.commissionInput) > parseInt(this.myCommission)) {
          this.$toast("填写的抵扣佣金不能大于拥有的佣金！");
          done(false);
        } else {
          if (this.type == 1) {
            this.getTotal();
          } else {
            this.getCartTotal();
          }
          done();
        }
      } else {
        done();
      }
    },
    // 地址列表
    getAddressList() {
      addressList().then(res => {
        res.map((i, x) => {
          if (i.status == 1) {
              console.log(i.province)
            if(i.province=='新疆维吾尔自治区'||i.province=='西藏自治区'||i.province=='内蒙古自治区'||i.province=='青海省'||i.province=='广西壮族自治区'||i.province=='海南省'||i.province=='台湾省'||i.province=='香港特别行政区'||i.province=='澳门特别行政区'){
                this.freight = 12;
                this.getTotal();
            }else{
                this.freight = 0;
                this.getTotal();
            }
            this.curAddress = i;
          }
        });
        // if (res[0]) {
