      //           paySign: res.paySign
      //         },
      //         function(res) {
      //           if (res.err_msg == "get_brand_wcpay_request:ok") {
      //             this.$toast("支付成功");
      //             localStorage.removeItem("goodsInfo");
      //             localStorage.setItem("payType", 1);
      //             this.orderList();
      //           }
      //         }
      //       );
      //     });
      //   } else {
      //     // 购物车支付
      //     nowBuyCart(
      //       this.cartIds,
      //       this.curAddress.id,
      //       this.ticketChoose.id || 0,
      //       this.integralInput || 0,
      //       this.commissionInput || 0
      //     ).then(res => {
      //       localStorage.setItem("payType", 1);
      //       localStorage.removeItem("chooseTicket");
      //       WeixinJSBridge.invoke(
      //         "getBrandWCPayRequest",
      //         {
      //           appId: res.appId,
      //           timeStamp: res.timeStamp,
      //           nonceStr: res.nonceStr,
      //           package: res.package,
      //           signType: res.signType,
      //           paySign: res.paySign
      //         },
      //         function(res) {
      //           if (res.err_msg == "get_brand_wcpay_request:ok") {
      //             this.$toast("支付成功");
      //             localStorage.removeItem("carGoods");
      //             localStorage.setItem("payType", 1);
