};
</script>
<style lang="scss" scoped>
.address-container {
  background: white;
  padding: 15px 10px 15px 8px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  box-shadow: 0 0 6px 1px #ccc;
  width: 345px;
  box-sizing: border-box;
  margin: 10px auto;
  .location-img {
    width: 36px;
    height: 36px;
    margin-right: 10px;
  }

  .address-info {
    width: 247px;
    line-height: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    font-size: 12px;

    .address-info-top {
      > span:first-child {
        margin-right: 20px;
      }
      > span:last-child {
        color: #a8a8a8;
      }
    }
  }

  .no-default-address {
    font-size: 12px;
    color: #a8a8a8;
    width: 267px;
  }

  .arrow-icon {
    font-size: 16px;
  }
}
.goodsItem {
  box-shadow: 0 0 6px 1px #ccc;
  width: 345px;
  box-sizing: border-box;
  padding: 16.6px 20px 14px 4.5px;
  margin: 15px auto;
  overflow: hidden;
  border-radius: 10px;
  .goodsImg {
    width: 117px;
    height: 69px;
    margin-top: 5%;
  }
  .goodsRight {
    width: 58%;
    font-size: 12px;
    color: #666;
    margin-left: 15px;
    .goodsName {
      color: #212121;
      font-size: 14px;
    }
    .goodsNum {
      margin: 17px 0 13.5px;
      span {
        color: #de4f53;
        font-size: 18px;
      }
    }
    .goodsPrice {
      color: #de4f53;
      font-size: 13px;
      span {
        font-size: 18px;
      }
    }
  }
}
.payMid {
  box-shadow: 0 0 6px 1px #ccc;
  width: 345px;
  border-radius: 10px;
  box-sizing: border-box;
  padding: 4px 0 4.5px 0;
  margin: 15px auto;
  font-size: 15px;
  color: #333;
  margin-bottom: 10px;
  .midItem {
    margin: 10px 14px 0 11.5px;
    padding-bottom: 14px;
    border-bottom: 1px solid #dcdcdc;
    .midNum {
      color: #de4f53;
      font-size: 15px;
      margin-left: 2px;
    }
  }
  .midItem:last-child {
    border-bottom: 0 none;
  }
}
.payBtm {
  position: fixed;
  width: 100%;
  height: 50px;
  z-index: 1000;
  color: #666666;
  font-size: 15px;
  bottom: 0;
  left: 0;
  line-height: 50px;
  border-top: 1px solid #f6f6f6;
  padding-left: 80px;
  background: #fff;
  .totalPrice {
    color: #de4f53;
  }
  button {
    background: #28a6ff;
    color: #fff;
    padding: 0 10px;
    height: 30px;
    line-height: 30px;
    border: 0 none;
    margin-left: 35px;
    border-radius: 15px;
  }
}
/deep/ .van-stepper__minus,
/deep/ .van-stepper__plus {
  color: #de4f53;
  background-color: transparent;
}
/deep/ .van-nav-bar__left {
  color: white;
}
/deep/ .van-dialog {
  top: 35%;
  position: fixed;
  .van-dialog__header {
    height: 45px;
  }
  .van-password-input__security {
    margin-bottom: 10px;
  }
  .van-password-input__info {
    padding-bottom: 20px;
  }
}
</style>
