import request from '@/utils/request'
export function myCart() {
    return request({
        url: 'api/goods/myCart',
        method: 'get'
    })
}
// 删除
export function deleteCart(id) {
    return request({
        url: 'api/goods/deleteCart',
        method: 'post',
        data:{id}
    })
}
// 推荐商品
export function recommendGoods(id) {
    return request({
        url: 'api/goods/recommendGoods',
        method: 'get'
    })
}
export function updateCart(id,nums,size_id,size_name) {
    return request({
        url: 'api/goods/updateCart',
        method: 'post',
        data:{
           id,nums,size_id,size_name
        }
    })
}
